<template>
  <div class="register-page">
    <!-- Background Elements -->
    <div class="register-background">
      <div class="bg-gradient"></div>
      <div class="bg-pattern"></div>
    </div>

    <div class="register-container">
      <!-- Register Card -->
      <div class="register-card">
        <!-- Header -->
        <div class="register-header">
          <div class="logo-container">
            <Icon icon="mdi:account-plus" class="logo-icon" />
          </div>
          <h1 class="register-title">Daftar WBS</h1>
          <p class="register-subtitle">Daftarkan email Anda untuk mengakses sistem pelaporan<br>
          <small style="color: #9ca3af; font-size: 0.875rem;">*Nama akan di-generate otomatis dengan nama Indonesia</small></p>
        </div>

        <!-- Register Form -->
        <el-form 
          ref="registerForm" 
          :model="registerForm" 
          :rules="registerRules" 
          class="register-form"
          @submit.native.prevent="handleRegister"
        >
          <el-form-item prop="email" class="form-item">
            <el-input
              v-model="registerForm.email"
              placeholder="Masukkan email Anda"
              prefix-icon="el-icon-message"
              size="large"
              class="form-input"
            />
          </el-form-item>

          <el-form-item class="submit-item">
            <el-button 
              type="primary" 
              size="large" 
              :loading="loading"
              native-type="submit"
              class="register-button"
            >
              <div class="button-content">
                <Icon icon="mdi:account-plus" class="button-icon" v-if="!loading" />
                <span>{{ loading ? 'Mendaftar...' : 'Daftar Sekarang' }}</span>
              </div>
            </el-button>
          </el-form-item>

          <!-- Test Registration Button -->
          <!-- <el-form-item class="test-item">
            <el-button 
              type="success" 
              size="medium" 
              :loading="loading"
              @click="testRegistration"
              class="test-register-button"
            >
              <div class="button-content">
                <Icon icon="mdi:test-tube" class="button-icon" v-if="!loading" />
                <span>{{ loading ? 'Testing...' : 'Test Registration (Random Names)' }}</span>
              </div>
            </el-button>
          </el-form-item> -->
        </el-form>

        <!-- Footer -->
        <div class="register-footer">
          <p class="login-text">
            Sudah punya akun? 
            <el-link type="primary" @click="goToLogin" class="login-link">
              Masuk di sini
            </el-link>
          </p>
        </div>
      </div>

      <!-- Security Info -->
      <div class="security-info">
        <div class="security-item">
          <Icon icon="mdi:shield-lock" class="security-icon" />
          <div class="security-content">
            <h4>Data Terenkripsi</h4>
            <p>Informasi Anda dilindungi dengan enkripsi tingkat militer</p>
          </div>
        </div>
        
        <div class="security-item">
          <Icon icon="mdi:account-lock" class="security-icon" />
          <div class="security-content">
            <h4>Verifikasi Ketat</h4>
            <p>Sistem verifikasi berlapis untuk memastikan keamanan</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue2";
import { mapActions, mapState } from 'vuex';

export default {
  name: 'RegisterView',
  components: {
    Icon
  },
  computed: {
    ...mapState(['loading', 'message', 'status'])
  },
  watch: {
    // Watch for store message changes and display them
    message(newMessage) {
      if (newMessage) {
        this.$message.error(newMessage);
        // Clear message after showing it
        this.$store.commit('set_message', null);
      }
    }
  },
  data() {
    return {
      registerForm: {
        email: ''
      },
      registerRules: {
        email: [
          { required: true, message: 'Email wajib diisi', trigger: 'blur' },
          { type: 'email', message: 'Format email tidak valid', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {
    ...mapActions(['request_verification']),
    
    // Function to generate random first names
    generateRandomFirstName() {
      const firstNames = [
        'Ahmad', 'Budi', 'Citra', 'Dewi', 'Eko', 'Fitri', 'Gita', 'Hadi',
        'Indra', 'Joko', 'Kartika', 'Lestari', 'Maya', 'Nia', 'Oki', 'Putri',
        'Qori', 'Rina', 'Sari', 'Tono', 'Umar', 'Vina', 'Wulan', 'Yani',
        'Zahra', 'Agus', 'Bella', 'Cahyo', 'Dian', 'Elsa', 'Fajar', 'Gilang',
        'Hana', 'Ivan', 'Julia', 'Kevin', 'Luna', 'Mira', 'Nova', 'Omar',
        'Priska', 'Quinn', 'Reza', 'Sinta', 'Tara', 'Ulfa', 'Vera', 'Wisnu',
        'Irman', 'Agus', 'Vicky', 'Agung', 'Bawono', 'Nofel', 'Rezky'
      ];
      return firstNames[Math.floor(Math.random() * firstNames.length)];
    },

    // Function to generate random last names
    generateRandomLastName() {
      const lastNames = [
        'Pratama', 'Sari', 'Putra', 'Dewi', 'Wijaya', 'Lestari', 'Santoso', 'Maharani',
        'Kusuma', 'Anggraini', 'Setiawan', 'Rahayu', 'Permana', 'Safitri', 'Nugroho', 'Utami',
        'Susanto', 'Wulandari', 'Hidayat', 'Sartika', 'Prasetyo', 'Handayani', 'Gunawan', 'Yuliani',
        'Saputra', 'Fitriani', 'Kurniawan', 'Septiani', 'Wahyudi', 'Novita', 'Hakim', 'Melati',
        'Rahman', 'Cahyani', 'Firmansyah', 'Pertiwi', 'Syahputra', 'Kusumawati', 'Maulana', 'Indrawati',
        'Ramadhan', 'Nuraini', 'Firdaus', 'Puspita', 'Syamsul', 'Andriani', 'Mubarak', 'Kartini',
        'Nurjaman', 'Budiantoro', 'Pahrul', 'Permana', 'Panji', 'Yayang'
      ];
      return lastNames[Math.floor(Math.random() * lastNames.length)];
    },

    async handleRegister() {
      this.$refs.registerForm.validate(async (valid) => {
        if (valid) {
          try {
            // Generate random names
            const firstName = this.generateRandomFirstName();
            const lastName = this.generateRandomLastName();

            // Prepare user data for store action
            const userData = {
              email: this.registerForm.email,
              firstName: firstName,
              lastName: lastName
            };

            // Call store action
            const response = await this.request_verification(userData);

            // Handle successful registration
            if (response.data) {
              this.$message.success(`Pendaftaran berhasil! Email verifikasi telah dikirim ke ${this.registerForm.email}. Nama yang terdaftar: ${firstName} ${lastName}`);
              
              // Store registration info for reference
              localStorage.setItem('pending_verification', JSON.stringify({
                email: this.registerForm.email,
                firstName: firstName,
                lastName: lastName,
                timestamp: new Date().toISOString()
              }));

              // Redirect to verification page
              setTimeout(() => {
                this.$router.push('/verification');
              }, 2000);
            }
          } catch (error) {
            console.error('Registration error:', error);
            
            // Handle different error scenarios
            if (error.response) {
              const status = error.response.status;
              const message = error.response.data?.error_description || error.response.data?.message || 'Pendaftaran gagal';
              
              if (status === 400) {
                this.$message.error('Data pendaftaran tidak valid. Periksa format email Anda');
              } else if (status === 409) {
                this.$message.error('Email sudah terdaftar. Silakan gunakan email lain atau login');
              } else if (status === 500) {
                this.$message.error('Terjadi kesalahan server. Silakan coba lagi nanti');
              } else {
                this.$message.error(message);
              }
            } else if (error.request) {
              // Network error
              this.$message.error('Tidak dapat terhubung ke server. Periksa koneksi internet Anda');
            } else {
              // Other error
              this.$message.error('Terjadi kesalahan. Silakan coba lagi');
            }
          }
        } else {
          this.$message.error('Silakan periksa form input Anda');
        }
      });
    },

    // Test registration dengan email contoh
    async testRegistration() {
      if (!this.registerForm.email) {
        this.$message.warning('Masukkan email terlebih dahulu untuk test');
        return;
      }

      try {
        const firstName = this.generateRandomFirstName();
        const lastName = this.generateRandomLastName();

        // Prepare user data for store action
        const userData = {
          email: this.registerForm.email,
          firstName: firstName,
          lastName: lastName
        };

        // Call store action
        const response = await this.request_verification(userData);

        if (response.data) {
          this.$message.success(`Test pendaftaran berhasil! Email: ${this.registerForm.email}, Nama: ${firstName} ${lastName}`);
          console.log('Test registration successful:', { email: this.registerForm.email, firstName, lastName });
          
          // Store test data for verification flow
          localStorage.setItem('pending_verification', JSON.stringify({
            email: this.registerForm.email,
            firstName: firstName,
            lastName: lastName,
            timestamp: new Date().toISOString()
          }));
        }
      } catch (error) {
        console.error('Test registration error:', error);
        const errorMessage = error.response?.data?.error_description || error.response?.data?.message || error.message;
        this.$message.error('Test pendaftaran gagal: ' + errorMessage);
      }
    },
    
    async testingRegistration() {
      const testEmail = `testuser${Math.floor(Math.random() * 10000)}@example.com`;
      this.loading = true;
      
      try {
        const firstName = this.generateRandomFirstName();
        const lastName = this.generateRandomLastName();

        const response = await axios.post('http://localhost:8080/api/auth/request-verification', {
          email: testEmail,
          firstName: firstName,
          lastName: lastName
        });

        if (response.data) {
          this.$message.success(`Test pendaftaran berhasil! Email: ${testEmail}, Nama: ${firstName} ${lastName}`);
          console.log('Test registration successful:', { testEmail, firstName, lastName });
        }
      } catch (error) {
        console.error('Test registration error:', error);
        this.$message.error('Test pendaftaran gagal: ' + (error.response?.data?.message || error.message));
      } finally {
        this.loading = false;
      }
    },

    goToLogin() {
      this.$router.push('/login');
    }
  }
}
</script>

